@font-face {
  font-family: "Roboto";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/Roboto-Light.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Bernier";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/BERNIERRegular-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Sansus";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Sansus-Webissimo-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "LeviReBrushed";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/LeviReBrushed.woff2") format("woff2");
  font-display: swap;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html,
body {
  height: 100%;
}

html {
  font-size: 16px;
}

body {
  font-family: "Roboto", Arial, sans-serif;
  color: #455a64;
  line-height: 1.42;
  position: relative;
  overflow-x: hidden;
  opacity: 1;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  min-height: 100vh;
}

.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden;
}

section {
  position: relative;
}

.visually-hidden:not(:focus):not(:active),
.svg-placeholder {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.about {
  display: flex;
}

.about__left,
.about__right {
  flex: 1 1 50%;
  padding-bottom: 150px;
}

.about__left {
  background: #efeee9;
}

.about__right {
  background: #eeebdf;
}

.about__triangle,
.works-head__triangle {
  position: absolute;
  bottom: 100%;
}

.about__section--image {
  position: absolute;
  left: 40px;
  top: 0;
  fill: #6E7171;
  opacity: 0.07;
  width: 357px;
  height: 125px;
}

.about__section--wrapper {
  text-align: center;
  padding: 0 30px;
}

.user-about__text {
  text-align: left;
}

.about-user-info__title {
  text-align: center;
}

.about-user-info__text {
  padding: 0 30px;
}

.about-data__content {
  margin-top: 50px;
  padding: 0 30px;
}

.auth-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.auth {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  border-radius: 10px;
  width: 380px;
}

.auth .user {
  text-align: center;
  position: static;
  transform: initial;
}

.auth .user .user__name {
  font-size: 28px;
  font-weight: 500;
  line-height: 34px;
}

.auth .user .user__title {
  line-height: 24px;
}

.user__photo {
  display: inline-block;
  border-radius: 50%;
  overflow: hidden;
  width: 150px;
  height: 150px;
}

.user__photo .user__avatar {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.auth__info {
  position: relative;
  padding: 40px 70px;
  background: rgba(252, 252, 252, 0.85);
}

.auth__btns {
  display: flex;
}

.auth__btn {
  background-color: #00bfa5;
  font-size: 13px;
  color: #fff;
  padding: 17px;
  display: block;
  text-align: center;
  text-decoration: none;
  flex: 1;
  border: 1px solid #6fd1c6;
  transition: all 0.3s ease-in-out;
}

.auth__btn:hover {
  background-color: #009688;
}

.auth__btn:last-child {
  border-right: none;
}

.auth__login {
  width: 180px;
  position: absolute;
  top: 30px;
  right: 30px;
}

.auth__login--link {
  width: 100%;
  border: 1px solid #ffffff;
  border-radius: 3px;
  background-color: transparent;
  color: #ffffff;
  font-weight: 500;
  line-height: 36.12px;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.auth__login--link:hover {
  background-color: #ffffff;
  color: #00bfa5;
}

.auth__socals {
  margin-top: 15px;
}

.auth__socals--link {
  display: inline-block;
  margin: 0 10px;
}

.auth__socials--image {
  display: block;
  fill: rgba(0, 191, 165, 0.5);
  transition: all .3s ease-in-out;
  cursor: pointer;
  width: 30px;
  height: 30px;
}

.auth__socials--image:hover {
  fill: #00bfa5;
}

.auth__socials--upwork {
  padding-top: 3px;
}

.btn {
  padding: 10px 15px;
  border-radius: 5px;
  color: #fff;
  display: inline-block;
  text-decoration: none;
}

.mountains .btn {
  background-color: #00bfa5;
  transition: all 0.2s ease-in-out;
}

.mountains .btn:hover {
  background-color: #009688;
}

.down-arrow {
  bottom: 40px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.down-arrow--image {
  fill: #FFFFFF;
  opacity: 0.7;
  width: 25px;
  height: 16px;
}

.footer {
  padding: 0 30px;
  background-color: #373e42;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hamburger {
  position: absolute;
  right: 30px;
  top: 25px;
  display: inline-block;
  width: 40px;
  height: 35px;
}

.hamburger__image {
  fill: #fff;
}

.mountains .hero__bg {
  top: 0;
  right: 0;
  bottom: 0;
}

.mountains .parallax-mount {
  display: block;
}

.hero {
  min-height: 650px;
  overflow: hidden;
  color: #ffffff;
  height: 100vh;
}

.hero__bg {
  position: absolute;
  left: 0;
}

.parallax-mount {
  display: none;
}

.desert .hero__bg {
  height: 100%;
  width: 6112px;
  background: url("../img/bg/sand.jpg") center left repeat-x;
  animation: bg 120s linear infinite;
}

@keyframes bg {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-3056px);
  }
}

.hero__title {
  text-align: center;
  width: 100%;
  padding: 0 30px;
}

.hero__title--image {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -160px;
}

.hero__user-block {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -125px;
  margin-top: -130px;
}

.hero__bg--image {
  max-width: 100%;
}

.nav-menu__item {
  display: inline-block;
  padding: 25px 25px 25px 0;
}

.nav-menu__link {
  color: #ffffff;
  text-decoration: none;
  font-weight: 500;
  line-height: 24px;
  transition: all 0.2s ease-in-out;
}

.nav-menu__link:hover {
  color: #00bfa5;
}

.wrapper-parallax {
  overflow: hidden;
  background-color: #fedcc8;
  width: 100%;
  height: 100%;
  position: relative;
}

.parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.parallax__layer {
  height: 100%;
  position: absolute;
  width: 3000px;
  left: 50%;
  margin-left: -1500px;
}

.parallax__image {
  position: absolute;
  bottom: 0;
}

.parallax-mount {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.parallax-mount__layer {
  height: 100%;
  position: absolute;
  width: 110%;
  left: 50%;
  margin-left: -55%;
}

.parallax-mount__image {
  height: auto;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.reviews {
  display: block;
  max-width: 100%;
  height: 500px;
  background: url("../img/bg/bg.jpg") center center no-repeat;
}

.hero__title--image {
  opacity: 0.06;
  display: inline-block;
  fill: #FEFEFE;
}

.skill-section__title {
  font-size: 28px;
  font-weight: 500;
  line-height: 32px;
}

.skills__item {
  margin-bottom: 30px;
}

.skill-section__list {
  display: flex;
}

.skill {
  position: relative;
  margin: 20px 0 0 20px;
}

.skill__name {
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mountains .skill__name {
  color: #00bfa5;
}

.skill__circle {
  width: 100px;
  height: 100px;
  display: block;
  fill: transparent;
  stroke-width: 15px;
}

.skills__circle-under {
  stroke: #dfdcd5;
}

.mountains .skills__circle-above {
  stroke: #10c1a8;
}

.desert .skills__circle-above {
  stroke: #e7a731;
}

.slider {
  display: flex;
  min-height: 100vh;
}

.slider__desc {
  flex-basis: 50%;
  display: flex;
  background-color: #f0efe9;
}

.slider__display {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #efebe0;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.slider__display--picture {
  display: inline-block;
  padding-bottom: 50px;
}

.slider__desc--info {
  width: 420px;
  margin: auto;
  text-align: center;
}

.slider__desc--skills {
  color: #20b5a1;
  font-weight: 500;
  padding: 20px 0;
  border-top: 2px solid rgba(0, 150, 136, 0.3);
  border-bottom: 2px solid rgba(0, 150, 136, 0.3);
}

.slider__desc--buttons {
  margin-top: 40px;
  text-align: center;
}

.slider__desc--btn {
  width: 200px;
  margin: 20px auto;
}

.slider__desc--btn .btn {
  width: 100%;
}

.slider__buttons {
  display: flex;
}

.slider__btn {
  background-color: transparent;
  border: none;
  padding: 0;
  flex-grow: 1;
  cursor: pointer;
  position: relative;
  transition: all .3s ease-in-out;
}

.slider__btn:hover .slider__arrow {
  fill: white;
}

.slider__btn--image {
  display: block;
  width: 100%;
}

.shadow {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.slider__arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 53px;
  height: 34px;
  fill: rgba(255, 255, 255, 0.5);
  transition: all .3s ease-in-out;
}

.socials {
  position: absolute;
  left: 30px;
  top: 30px;
  display: flex;
  align-items: center;
}

.socials__item {
  margin: 0 15px;
}

.socials__image {
  height: 22px;
  fill: #FFFFFF;
  opacity: .5;
  transition: all 0.2s ease-in-out;
}

.socials__image:hover {
  opacity: 1;
}

.socials__image--github {
  width: 26px;
}

.socials__image--upwork {
  width: 26px;
}

.socials__list {
  display: flex;
  align-items: center;
}

.socials__list .socials__link {
  display: inline-block;
  height: 35px;
}

.socials__list .socials__item {
  margin-left: 5px;
}

.socials__list .socials__image {
  width: 35px;
  height: 35px;
}

.socials__list .socials__image--upwork {
  width: 40px;
  height: 40px;
}

.title,
.subtitle {
  font-family: "Bernier",Arial,sans-serif;
  font-weight: 400;
  letter-spacing: 1.57px;
  position: relative;
  display: inline-block;
}

.title:after,
.subtitle:after {
  right: 40px;
}

.title:before,
.subtitle:before {
  left: 40px;
}

.title {
  font-size: 63px;
  padding: 45px 80px;
  color: #373e42;
  line-height: 66px;
}

.subtitle {
  color: #455a64;
  padding: 40px 80px;
  font-size: 35px;
  line-height: 41.01px;
}

.mountains .title:after,
.mountains .title:before,
.mountains .subtitle:after,
.mountains .subtitle:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 22px;
  height: 5px;
}

.mountains .title:after,
.mountains .title:before {
  background-color: rgba(55, 62, 66, 0.2);
}

.mountains .subtitle:after,
.mountains .subtitle:before {
  background-color: rgba(69, 90, 100, 0.2);
}

.triangle {
  height: 100px;
  width: 50%;
}

.triangle-fill-white {
  fill: #efeee9;
}

.triangle-fill-beige {
  fill: #eeebdf;
}

.about__left .triangle {
  left: 0;
}

.about__right .triangle {
  right: 0;
}

.user__photo img {
  border-radius: 50%;
}

.user__name {
  font-size: 35px;
  font-weight: 500;
  margin-top: 25px;
}

.user__desc {
  font-size: 16px;
  font-weight: 100;
  line-height: 24px;
  margin-top: 10px;
}

.user-about__text p:last-child {
  margin-top: 25px;
}

.works-head {
  text-align: center;
  background-color: #faf8f0;
}

.works__section--image {
  opacity: .07;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.works-head__triangle {
  fill: #faf8f0;
}

.works-head__triangle:first-of-type {
  left: 0;
}

.works-head__triangle:last-of-type {
  right: 0;
}

@media screen and (max-width: 992px) {
  html {
    font-size: 15px;
  }

  .about__section--image {
    left: 20px;
  }

  .hero__bg--image {
    max-width: 150%;
  }

  .skill-section__list {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }

  .about {
    flex-wrap: wrap;
  }

  .about__left,
  .about__right {
    flex-basis: 100%;
    padding-bottom: 50px;
  }

  .footer {
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer .footer__menu {
    flex-basis: 100%;
    text-align: center;
  }

  .hero__bg--image {
    max-width: 180%;
  }

  .skills {
    text-align: center;
  }

  .skill-section__list {
    justify-content: center;
  }

  .slider {
    flex-wrap: wrap;
  }

  .slider__desc {
    flex-basis: 100%;
    order: 2;
    padding-bottom: 300px;
  }

  .slider__display {
    flex-basis: 100%;
    order: 1;
    display: block;
    padding-bottom: 0;
  }

  .slider__display--picture {
    width: 100%;
  }

  .slider__buttons {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .triangle-fill-beige {
    fill: #efeee9;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .about__section--wrapper {
    padding: 0 15px;
  }

  .about-user-info__text {
    padding: 0 15px;
  }

  .about-data__content {
    padding: 0 15px;
  }

  .auth__login {
    top: 15px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .footer {
    padding: 0 15px;
  }

  .hamburger {
    right: 15px;
  }

  .hero__bg--image {
    max-width: 250%;
  }

  .slider__display--picture {
    padding-bottom: 250px;
  }

  .socials {
    left: 15px;
  }
}